<template>
  <div>
    <ul class="tag" v-if="showTag">
      <router-link to="/views/collegeIntroduction/basic">
        <li :class="{ active: state == 1 }" @click="selectTag(1)">基本信息</li>
      </router-link>

      <router-link to="/views/collegeIntroduction/branch">
        <li :class="{ active: state == 2 }" @click="selectTag(2)">分支机构</li>
      </router-link>
    </ul>
    <div class="tagNull" v-if="showTag"></div>
    <router-view/>
  </div>
</template>

<script>
export default {
  name: 'CollegeIntroduction',
  data () {
    return {
      state: 1,
      tagsUrl: [
        '/views/collegeIntroduction/basic',
        '/views/collegeIntroduction/branch'
      ],
      showTag: false
    }
  },
  methods: {
    selectTag (state) {
      if (state == this.state) {
        return
      }
      this.state = state
    }
  },
  created () {
    const arrIndex = $.inArray(this.$route.path, this.tagsUrl)
    if (arrIndex > -1) {
      this.showTag = true
      this.state = arrIndex + 1
    }
  }

}
</script>



